import {apiUrl} from '../environment';
import axios from 'axios';

// login
const login = async (credentials) => {
    try {
        const { data: post }  = await axios.post(apiUrl + '/login', credentials);
        return post
    } catch (e) {
        const {data : error} = e.response
        throw error
    }
}

// register
const signUp = async (user) => {
    try {
        const { data: post }  = await axios.post(apiUrl + '/sign_up', user);
        return post
    } catch (e) {
        const {data : error} = e.response
        throw error
    }
}

// forget
const forget = async (emailObj) => {
    try {
        const { data: post }  = await axios.post(apiUrl + '/forget_password', emailObj);
        return post
    } catch (e) {
        const {data : error} = e.response
        throw error
    }
}

// reset
const reset = async (emailObj) => {
    try {
        const { data: post }  = await axios.post(apiUrl + '/reset_password', emailObj);
        return post
    } catch (e) {
        const {data : error} = e.response
        throw error
    }
}

export {
    login,
    signUp,
    forget,
    reset
}